@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100..900&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #333;
}

body {
  margin: 0;
  font-family: "Roboto Slab", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  max-width: 1240px;
  margin: auto;
}

.btn {
  color: #fff;
  padding: 12px 24px;
  background-image: linear-gradient[45deg, #5651e5 0%, #709dff 100%];
  border: none;
  border-radius: 8px;
  font-sizze: 1rem;
  transition: 0.4s;
  cursor: pointer;
}

.btn:alive {
  transform: translate(4px);
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.2);
  border-bottom: none;
}

.carousel-container {
  position: center;
  //background-color: #333333;
  margin: auto;
  margin-bottom: 30px;

  //left: 50%;
  //transform: translateX(-50%);
  width: 90%;

  .slick-prev,
  .slick-next {
    font-size: 0px;
    color: #000000;
    background-color: rgba(0, 0, 0, 1);
    width: 40px;
    height: 200px;
    line-height: 200px; /* Match height for centering */
    text-align: center;
    cursor: pointer;
    position: absolute;
    top: 30%;

    transform: translateY(-40%);
    z-index: 1; /* Ensure the buttons are on top of the carousel content */
  }

  .slick-prev {
    left: 0;
  }

  .slick-next {
    right: 0;
  }
}

.card-image {
  margin-left: 50px;
  width: 80%;
  height: auto;
  max-height: 600px;
  object-fit: cover;
  height: 100%;
}

